import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { motion } from 'framer-motion';
import "../scss/Main.scss";


const TopRated = () =>{


    const [ topRated, setTopRated ] = useState([])

    useEffect(() =>{
        fetch('https://api.themoviedb.org/3/movie/top_rated?api_key=0ed942484b766040339b35ccf2f6a4e8&language=en-US')
        .then(res => res.json())
        .then(data => setTopRated(data.results))
    },[])

  
    return(
        <>
            <motion.div 
                className="container__subpage"
                intial={{opacity:0}}
                animate={{opacity:1}}
                exit={{opacity:0}}
            >
                {
                topRated.map(movie =>{
                    return( 
                        <Link className="movie" style={{textDecoration:'none'}} to={`top_rated/${movie.id}`} key={movie.id} >
                            <div>
                                <img className="movie__img" src={`https://image.tmdb.org/t/p/original${movie.poster_path}`} alt="null"/>
                            </div>
                            <div className="movie__overlay">
                                <div className="movie__title">{movie ? movie.original_title : ""}</div>
                                <div className="movie__runtime">
                                    {movie ? movie.release_date : ""}
                                    <span className="movie__rating">
                                        {movie ? movie.vote_average : ""}
                                        <svg color="yellow" stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 1024 1024" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M908.1 353.1l-253.9-36.9L540.7 86.1c-3.1-6.3-8.2-11.4-14.5-14.5-15.8-7.8-35-1.3-42.9 14.5L369.8 316.2l-253.9 36.9c-7 1-13.4 4.3-18.3 9.3a32.05 32.05 0 0 0 .6 45.3l183.7 179.1-43.4 252.9a31.95 31.95 0 0 0 46.4 33.7L512 754l227.1 119.4c6.2 3.3 13.4 4.4 20.3 3.2 17.4-3 29.1-19.5 26.1-36.9l-43.4-252.9 183.7-179.1c5-4.9 8.3-11.3 9.3-18.3 2.7-17.5-9.5-33.7-27-36.3z"></path></svg>
                                    </span>
                                </div>
                            </div>
                        </Link>         
                    )})
                }
            </motion.div>
        </>   
    )
}

export default TopRated