import React from "react";
import { Route, Routes, useLocation } from "react-router-dom";
import Home from "./components/Home/Home";
import SingleMovie from "./components/Elements/SingleMovie/SingleMovie";
import Series from './components/Series/Series'
import Popular from "./components/Popular/Popular";
import SingleSeries from "./components/Elements/SingleSeries/SingleSeries";
import Upcoming from "./components/Upcoming/Upcoming";
import { AnimatePresence } from 'framer-motion';
import TopRated from "./components/TopRated/TopRated";


const Router = () => {

  const location = useLocation();

    return(
      <AnimatePresence>
        <Routes location={location} key={location.pathname}> 
          <Route path='/' element={<Home />}/>
          <Route path='/series' element={<Series />} />
          <Route path='series/:id' element={<SingleSeries />}/>
          <Route path='series/series/:id' element={<SingleSeries />}/>
          <Route path='/popular' element={<Popular />} />
          <Route path='/popular/:id' element={<SingleMovie />} />
          <Route path='/popular/popular/:id' element={<SingleMovie />} />
          <Route path='/upcoming' element={<Upcoming />}/>
          <Route path='/upcoming/:id' element={<SingleMovie />}/>
          <Route path='upcoming/upcoming/:id' element={<SingleMovie />}/>
          <Route path='/top_rated' element={<TopRated />}/>
          <Route path='/top_rated/:id' element={<SingleMovie />}/>
          <Route path='top_rated/top_rated/:id' element={<SingleMovie />}/>
          <Route path="movies/:id" element={<SingleMovie />}/>
        </Routes>
      </AnimatePresence>
    )
}

export default Router

       